.Metrics {

  // padding: 41px;
  .cards-body {
    padding: 2.5rem;
    margin: 2.5rem;
    border-radius: 1.5rem;
    border-radius: 20px;

    @media screen and (max-width: 768px) {
      padding: 12px;
      margin: 20px 0;
    }

    // background: white;

    // @media (min-width: 768px) {
    //   margin-top: 128px;
    // }
    .countries {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      gap: 50px;

      .inter{
        display: flex;
        flex-direction: column;
        gap: 20px;

        h2{
          font-size: 26px !important;
          font-weight: 700;
          color: rgba(0, 0, 0, 0.852);
        }
      }
    }

    .title {
      font-size: 2.25rem;

      @media screen and (max-width: 574px) {
        font-size: 25px;
        text-align: justify;
      }
    }

    .categories {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
      margin-bottom: 40px;
    }

    .title-metric-category {
      font-weight: bold;
      font-size: 25px;

      @media screen and (max-width: 574px) {
        font-size: 25px;
        text-align: justify;
      }
    }

    .metric-descrition {
      font-size: 18px;
    }
  }

  .parent-metrics {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;

    @media screen and (max-width: 1000px) {
      display: flex;
      flex-direction: column;
    }

    .col1 {
      grid-area: 1 / 1 / 2 / 2;
    }

    .col2 {
      grid-area: 1 / 2 / 2 / 3;
    }
  }

  .parent-metrics-c {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;

    .col-1-c {
      grid-area: 1 / 1 / 2 / 2;
    }

    .col-2-c {
      grid-area: 1 / 2 / 2 / 3;
    }
  }

  .padingTablemetrics {
    padding: 0 20%;

    @media screen and (max-width: 574px) {
      padding: 0 0;
    }
  }

  .metrics-table-font {
    font-size: 20px;
    margin: 10px 0;
    text-align: center;

    @media screen and (max-width: 574px) {
      font-size: 16px;
      text-align: center;
    }
  }

  hr {
    border-color: #3887cc;
  }

  .align-items {
    align-items: center;
    display: flex;

    @media screen and (max-width: 1000px) {
      justify-content: center;
      margin-top: 30px;
    }
  }

  .text-plane {
    margin-top: 30px;

    p {
      padding: 10px 0;

      @media screen and (max-width: 574px) {
        font-size: 16px;
        text-align: justify;
      }
    }
  }
}